import D from '../../';
import methods from '../../methods';
import { default as parent, transform } from '../Object';
import Promise from '../Promise';
import Arr from '../Array';
import { assign, validate, toArray } from '../../libs';

const cls = class Function extends parent {
	constructor(func = () => {}) {
		super((() => {
			validate([func], ['function']);

			function proxy() {
				if (proxy.called < proxy.canBeCalled) {
					proxy.called++;

					let { before, after, original, context, args, sync, contextLocked } = proxy;
					let ret;

					context = contextLocked ? context : this;
					args = args.concat(toArray(arguments));

					if (sync) {
						for (let i = 0; i < before.length; i++) {
							args = before[i].apply(null, toArray(args));
						}

						ret = original.apply(context, toArray(args));

						for (let i = 0; i < after.length; i++) {
							ret = after[i](ret);
						}

						return ret;
					}

					let promise = Promise.resolve(args);

					for (let i = 0; i < before.length; i++) {
						promise = promise.then((args) => {
							return before[i].apply(null, toArray(args));
						});
					}

					promise = promise.then((args) => {
						return original.apply(this, toArray(args));
					});

					for (let i = 0; i < after.length; i++) {
						promise = promise.then((ret) => {
							return after[i](ret);
						});
					}

					return promise;
				}
			}

			assign(proxy, {
				after: [],
				args: [],
				argsLocked: [],
				before: [],
				called: 0,
				canBeCalled: Infinity,
				context: null,
				contextLocked: false,
				original: func,
				sync: true
			});

			return proxy;
		})());
	}

	after(f, where = 1) {
		f = transform(f);

		validate([f], ['function']);

		const func = this.$;

		if (where < 0) {
			func.after.unshift(f);
		} else {
			func.after.push(f);
		}

		return this;
	}
	apply(context, args) {
		return this.$.function.apply(context, args);
	}
	async(cond = true) {
		this.$.sync = !cond;
	}
	before(f, where = -1) {
		f = transform(f);

		validate([f], ['function']);

		const func = this.$;

		if (where > 0) {
			func.before.push(f);
		} else {
			func.before.unshift(f);
		}

		return this;
	}
	bind(context, args) {
		this.bindContext(context);
		this.bindArgs(args);

		return this;
	}
	bindArgs(args) {
		const func = this.$;

		func.args = func.args.concat(Arr.from(args).$);

		return this;
	}
	bindContext(context) {
		const func = this.$;

		if (!func.contextLocked) {
			func.context = context;
		}

		return this;
	}
	call(context) {
		return this.$.function.apply(context, Array.prototype.slice.call(arguments, 1));
	}
	canBeCalled(n) {
		this.$.canBeCalled = n;

		return this;
	}
	interval(number) {
		const f = this.$.function.bind({ clear });

		let timeout,
			args = Array.prototype.slice.call(arguments, 1);

		(() => {
			f.apply(null, args);
			timeout = setTimeout.apply(null, [f, this.$].concat(args));
		})();

		function clear() {
			return clearTimeout(timeout);
		}

		return clear;
	}
	lock(context, args) {
		this.lockContext(context);
		this.lockArgs(args);

		return this;
	}
	lockArgs(args) {
		const func = this.$;

		func.args = func.argsLocked = func.argsLocked.concat(Array.from(args).$);

		return this;
	}
	lockContext(context) {
		const func = this.$;

		if (!func.contextLocked) {
			func.context = context;
			func.contextLocked = true;
		}

		return this;
	}
	timing(mark) {
		mark = !arguments.length ? this.$.original.name || 'anonymous' : String(mark);

		this.before(function () {
			console.time(mark);

			return arguments;
		}, 1);

		this.after((ret) => {
			console.timeEnd(mark);

			return ret;
		}, -1);

		return this;
	}
	unbind() {
		this.unbindContext();
		this.unbindArgs();

		return this;
	}
	unbindArgs() {
		const func = this.$;

		func.args = func.argsLocked;

		return this;
	}
	unbindContext() {
		const func = this.$;

		if (!func.contextLocked) {
			func.context = null;
		}

		return this;
	}
};

D.Function = cls;
D.constructors.unshift({
	check: methods.isFunction,
	cls
});

export default cls;


